
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#container {
  position: absolute;
  width: 100%;
  height: 100%;
  display:flex;
  flex-direction:column;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login {
    flex:1 1;
    background-color: #ffffff;
    display: flex;
    position: relative;
    flex-direction: column;
    padding: 50px 20px 40px 20px;
    justify-content: center;
    align-items: center;
}

.settings-btn {
    position:absolute;
    background-color: transparent;
    border: 0;
    top: 20px;
    right: 20px;
    color: #777777;
}

.settings-btn:hover {
    cursor: pointer;
}

button:focus {
    outline: 0;
}

.image-dummy {
    background-color: skyblue;
    width: 160px;
    height: 160px;
    margin: 50px 0;
}
* {
  box-sizing: border-box;
  margin: 0;
}

#wrap {
  width: 300px;
  text-align: center;
  /*height: 500px; */
  /* background-color: #eeeeee; */
  margin: auto;
}

.col {
  width: 250px;
  margin: auto;
}

.label {
  font-family: 'Jua', sans-serif;
  color: #353645;
  font-size: 23px;
}

.text {
  border: 1px solid black;
  width: 170px;
  height: 23px;
  vertical-align: text-bottom;
}

.main-tabs {
    background-color: #ffffff;
    margin: 0;
    padding: 0;
    display: flex;
    height: 50px;
    border-bottom: 0.5px solid #e7e7e7;
}

.main-tabs > li {
    list-style: none;
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-tabs > li > a {
    text-decoration: none;
    color: #000000;
    font-size: 8pt;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
}

.profile-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.5px solid #e7e7e7;
}

.profile-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.profile-image {
    width:50px;
    height:50px;
    border-radius:50%;
    margin: 10px 15px;
}

.profile-nickname {
    font-weight: bold;
    font-size: 12pt;
}

.settings-btn:hover {
    cursor: pointer;
}
.container {
    width: 300px;
    height: 500px;
    background-color: #ffffff;
    position: relative;
}

.menubar{
    width: 300px;
    height: 60px;
    background-color: #DCDCDC;
}

.menufont{
    font-size: 0.85em;
    color: #FFFFFF;
}

#talkfont{
    padding: 3px 5px 2px 6px;
}

#payfont{
    padding: 3px 0 2px 0;
}

#loginfont{
    padding: 3px 5px 2px 12px;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

#menuul li{
    float: left;
    margin: 10px 5px 5px 0px;
}

.logo{
    width: 70px;
    height: 40px;
    background-image: url(/static/media/albatalklogo.ab12cfbf.png);
    margin: auto;
}

.menubtn{ /* 메뉴의 버튼들 */
    width: 60px;
    height: 20px;
    background-color: #353646;
    padding: 10px 5px 10px 5px;
    border-radius: 20px;
}

.typearea{
    width: 100%;
    height: 100px;
    background-color: #ffffff;
}

p{
    margin: 0;
}

#lowestpay{
    padding: 15px 25px 0px 30px;
}

#fromtoul{
    padding: 15px 30px;
    margin: 0 25px;
}

#fromtoul li{
    float: left;
    padding: 5px;
}

.formarea{
    width: 100%;
    height: 230px;
    background-color: #DCDCDC;
}

.formarea a{
    text-decoration: none;
    color: black;
}

#formul li{
    padding: 15px 5px 0px 15px;
}

li input{
    margin: 0 5px 0 0;
}

.title{
    height: auto;
    width: 110px;
    float: left;
}

.calculbtn{
    width: 100px;
    height: 40px;
    background-color: #353646;
    margin: 30px 50px 0 100px;
    border-radius: 5px;
}

.calculbtn p{
    padding: 11px 20px 0 22px;
    color: #ffffff;
}

.resultarea{
    width: 100%;
    height: auto;
}

#resultul{
    margin: 10px 30px 10px 30px;
    padding: 5px 2px 0 18px;
}

#resultul li{
    float: left;
    margin: 0 5px 0 0;
}

#resultpay{
    font-size: 1.2em;
    color: #353646;
}
.settings {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1;
}

.settings-header {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 0.5px solid #e7e7e7;
}

.back-button {
    margin: 0px 15px;
}

.back-button:hover {
    cursor: pointer;
}

.settings-header-title {
    font-weight: bold;
}

.settings-list {
    flex: 1 1;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    padding-top: 15px;
}

.settings-list > .settings-list-item {
    height: 50px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-top: 0.5px solid #e7e7e7;
    border-bottom: 0.5px solid #e7e7e7;
}

.settings-list > .settings-list-item > .settings-list-item-title {
    margin: 0px 15px;
}

.settings-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 45px;
    border-top: 0.5px solid #e7e7e7;
}

.logout-button {
    margin: 0px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10pt;
}

.logout-button:hover {
    cursor: pointer;
}
